import { mapOrigin, mapPath, mapRes, mapLevel, maxZoom, minZoom } from '@/api/config.js'
// import mapArea from '../ajax/blocks.js'
import { map } from '../map/mapset.js'

const crs = new L.Proj.CRS('SR-ORG:7408',
'+proj=longlat +ellps=GRS80 +no_defs',
{
  resolutions: mapRes,
  origin: mapOrigin,
}
);
const defaultToDarkFilter = [
  'blur:0px', //  
  'brightness:100%', // ,
  'contrast:100%', // 0
  'grayscale:0%', // 
  	// 'hue:180deg',//
  'opacity:45%', // 
  'invert:100%', // 
  'saturate:100%', // 
//	'sepia:0%',//
]
const attrib = "&copy ";

export default {
  // mixins: [mapArea],
  data() {
    return {
      privateMapPath: '',
      mapAreaConfig: {
        mapId: 'map_area',
        // maxZoom,
        mapLevel: mapLevel[2],
        minZoom,
        lat: 30.190383,
        lng: 120.270469
      },
    }
  },
  methods: {
    initConfig(config) {
      const { mapId, lat, lng, zoom, minZoom, maxZoom, mapLevel } = config
      const map1 = map(mapId, {
        crs,
        maxZoom,
        minZoom,
        renderer: L.canvas()
        /*fullscreenControl: {
          pseudoFullscreen: false
        },*/
      })
     
      const baseMap = L.tileLayer.colorFilter(mapPath,
      {
        tileSize: 256,
        attribution: attrib,
        filter: defaultToDarkFilter,
      })
        map1.setStyle('indigo')
        map1.addLayer(baseMap)
        //
        map1.setView([lat,lng], mapLevel)
        return map1
    },
  },
  mounted() {
    this.mapArea = this.initConfig(this.mapAreaConfig)
  },
  beforeDestroy () {

  }
}